import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpModule } from '@angular/http';
import { Http } from '@angular/http';
import { MyApp } from './app.component';
import { ConfiguracionMaypiProvider } from '../providers/configuracion-maypi/configuracion-maypi';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/buscar/buscar.module#BuscarPageModule', name: 'BuscarPage', segment: 'lista', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/destacado/destacado.module#DestacadoPageModule', name: 'DestacadoPage', segment: 'destacado', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/eventos/eventos.module#EventosPageModule', name: 'EventosPage', segment: 'eventos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/explorar/explorar.module#ExplorarPageModule', name: 'ExplorarPage', segment: 'explorar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/informacion-establecimiento/informacion-establecimiento.module#InformacionEstablecimientoPageModule', name: 'InformacionEstablecimientoPage', segment: 'informacion-establecimiento/:idEstablecimiento', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/inicio.module#InicioPageModule', name: 'InicioPage', segment: 'lista', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ConfiguracionMaypiProvider
  ]
})
export class AppModule {}



